/* Base Styles and CSS Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: #2d3748;
  background-color: #f7fafc;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 0.5em;
  color: #2d3748;
}

h1 { font-size: 2rem; }
h2 { font-size: 1.75rem; }
h3 { font-size: 1.5rem; }
h4 { font-size: 1.25rem; }
h5 { font-size: 1.125rem; }
h6 { font-size: 1rem; }

p {
  margin-bottom: 1rem;
}

a {
  color: #5a67d8;
  text-decoration: none;
  transition: color 0.2s;
}

a:hover {
  color: #4c51bf;
}

/* Brand Colors */
:root {
  --primary: #5a67d8;
  --primary-hover: #4c51bf;
  --primary-light: #c3dafe;
  --primary-very-light: #ebf4ff;
  
  --success: #38a169;
  --success-light: #c6f6d5;
  
  --danger: #e53e3e;
  --danger-light: #fed7d7;
  
  --warning: #dd6b20;
  --warning-light: #feebc8;
  
  --info: #3182ce;
  --info-light: #bee3f8;
  
  --dark: #2d3748;
  --gray-dark: #4a5568;
  --gray: #718096;
  --gray-light: #a0aec0;
  --gray-lighter: #cbd5e0;
  --gray-lightest: #e2e8f0;
  
  --background: #f7fafc;
  --white: #ffffff;
}

/* Containers and Layout */
.container {
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

/* Accessibility Enhancements */
.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
}

/* Scrollbar Customization */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Focus states for accessibility */
:focus {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

/* Print styles */
@media print {
  body {
    background-color: #fff;
    color: #000;
  }
  
  .no-print {
    display: none;
  }
}

button {
  cursor: pointer;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.3s;
}

.btn-primary {
  background-color: #5a67d8;
  color: white;
}

.btn-primary:hover {
  background-color: #4c51bf;
}

.btn-secondary {
  background-color: #e2e8f0;
  color: #4a5568;
}

.btn-secondary:hover {
  background-color: #cbd5e0;
}

input, textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #e2e8f0;
  border-radius: 5px;
  font-size: 14px;
  margin-bottom: 15px;
}

.card {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
}

.privacy-tag {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
  margin-left: 8px;
}

.private {
  background-color: #fed7d7;
  color: #e53e3e;
}

.public {
  background-color: #c6f6d5;
  color: #38a169;
} 